.cases {
	&-page {
		.preview__wrapper-text {
			@include r(668) {
				padding-top: 36px; } }
		.tabs__caption {
			max-width: 515px;
			min-width: 515px; }
		.news_tab .tabs__field {
			.item {
				@include r(992) {
					margin-bottom: 24px; }
				@include sm {
					max-width: 49%; }
				@include r(668) {
					max-width: 100%;
					margin-right: 0; } }
			&:not(:last-child) {
				@include r(992) {
					margin-bottom: 0; } } }
		.news {
			&.news_tab {
				.tabs__field {
					@include r(992) {
						flex-direction: row; }
					@include r(930) {
						flex-direction: column; }
					@include sm {
						flex-direction: row; }
					@include r(668) {
						flex-direction: column; }
					&.tabs_large .item {
						max-width: 100%; }
					.item {
						max-width: 50%;
						@include r(930) {
							max-width: 100%;
							margin-right: 0; }
						@include sm {
							max-width: 50%;
							margin-right: 16px; }
						@include r(668) {
							max-width: 100%;
							margin-right: 0; } } } } } } }
