.sitemap {
	padding-bottom: 60px;
	&-page {
		.preview__wrapper-text {
			padding-top: 38px;
			margin-bottom: 0; } }
	.inner {
		@include r(668) {
			padding: 0 16px; } }
	ul.list-dotted {
		li {
			a {
				@include font-family($noto-bld); }
			& > ul li a {
				@include font-family($noto-reg); } }
		&.list-null li a {
			@include font-family($noto-reg); } } }




.cols {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	flex-wrap: wrap;
	.col {
		width: 30%;
		@include r(992) {
			width: 50%; }
		@include r(900) {
			width: 100%;
			margin-bottom: 24px; }
		@include sm {
			width: 50%; }
		@include r(668) {
			width: 100%; }
		@include r(580) {
			width: 50%; }
		@include r(481) {
			width: 100%; }
		.title-semi-md {
			margin-bottom: 14px;
			&.mt-1x {
				margin-top: 28px; }
			a {
				color: inherit;
				text-decoration: none;
				&:hover {
					text-decoration: underline; } } } } }
