.main-news-post {
	@include r(668) {
		.news__title {
			h2 {
				margin-bottom: 0px; } } }
	.tabs__field.news_tab {
		padding-bottom: 0px;
		@include r(992) {
			flex-direction: row; }
		@include r(830) {
			flex-direction: column; }
		@include sm {
			flex-direction: row; }
		@include r(668) {
			flex-direction: column; } }
	.news .item {
		@include r(992) {
			max-width: 50%; }
		@include r(830) {
			max-width: 100%; }
		@include sm {
			max-width: 50%; }
		@include r(668) {
			max-width: 100%; }
		&:not(:last-child) {
			@include r(830) {
				margin-right: 0;
				margin-bottom: 16px; }
			@include sm {
				margin-bottom: 0;
				margin-right: 16px; }
			@include r(668) {
				margin-right: 0;
				margin-bottom: 16px; } } }
	.preview__date {
		margin-right: 28px; } }












