.main-cases {
	.tabs__field.news_tab {
		padding-bottom: 0px;
		@include r(992) {
			flex-direction: row; }
		@include sm {
			flex-direction: column; }
		.item {
			max-width: 50%;
			@include r(900) {
				max-width: 100%; }
			@include sm {
				margin-right: 0;
				margin-bottom: 16px; } } }
	@include r(668) {
		.news__title {
			h2 {
				margin-bottom: 0px; } } }
	.preview__date {
		margin-right: 28px; } }
